body {
    font-family: Arial, sans-serif;
    margin: 20px;
  }
  
  input {
    display: block;
    margin: 10px 0;
    padding: 8px;
    width: 100%;
    max-width: 300px;
  }
  
  button {
    margin-right: 10px;
    padding: 8px 12px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .previousbuttion{
    border: none;
    background-color: #f9fbf9;
    color: black;
  }
 .butcontain{
    display: flex;
    justify-content: space-between;
 }
  
  div {
    max-width: 400px;
    margin: 0 auto;
   padding: 20px;
   border: 1px solid grey;
   border-radius: 5px;
  }
  